$grey: 	hsl(240, 10%, 30%); // #464655
$lightBlue: hsl(205, 77%, 70%); //#79BEEE
$cottonCandy: 	hsl(310, 100%, 90%); // #FFCAF6
$EggShell: hsl(55, 32%, 85%); // #E6E4CE

* {
  box-sizing: border-box;
}

html, body, #root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  overflow: hidden;
  font-size: 14px;
}

#root {
  overflow: auto;
}

body {
  position: fixed;
  overflow: hidden;
  overscroll-behavior-y: none;
  // font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir,
  //   helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  font-family: Helvetica, sans-serif;
  color: black;
  -webkit-font-smoothing: antialiased;
  background: #f1f4f8;
}


// SLIDEBAR CSS

// I have no idea how this mess of a component works
// but its styling only works here i guess
#page-wrap {
  text-align: center;
  overflow: auto;
  top: 45%;
  position: relative;
}

.bm-item {
  display: inline-block;

  /* Our sidebar item styling */
  text-decoration: none;
  margin-bottom: 10px;
  color: $EggShell;
  transition: color 0.2s;

  outline: none; 
  // Removes The Border on Chrome
  // Might be Smarter to port this into its own CSS Class
  // TODO: Add a border on the left of the Selected Type, check Refactoring UI book
}


.section-title {
  display: inline-block !important; // overrides display: block;
}

.section-title:hover {
  color: $EggShell !important;
}


.clickable:hover {
  cursor: pointer;
}

// active component, refer to SlideBar
.active {
  color: $lightBlue;
}

.bm-item + h4:hover {
  color: $lightBlue;
  // TODO: add BorderBox here
}

.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  right: 36px;
  top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/* General sidebar styles */
.bm-menu {
  background: $grey;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
  overflow: unset !important ;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: $EggShell; //#b8b7ad
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}


.visibility-button {

  // Main Elements
  cursor: pointer;
  background-color: hsl(240, 20%, 30%);
  border: 1px solid hsl(240, 25%, 30%);
  color: $EggShell;

  // Positioning, Sizing
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  min-width: 100%;

  // Animation and Opacity
  opacity: 1;
  filter: alpha(opacity=40);
  transition: 0.25s;
}
// Styling on Clicking Button
.visibility-button:active {
  background-color: $grey;
  box-shadow: 0 5px #000;
  transform: translateY(4px);
}
// Hover Effect
.visibility-button:hover {
  opacity: 1;
  filter: alpha(opacity=100);
}